@import "~@/styles/_default.scss";





















































































































































































































































































































































.title-block {
  overflow: hidden;
  padding-bottom: 15px;
  margin: 0 0 30px 0;
  border-bottom: 1px solid #d7dde4;

  .main_title {
    float: left;
    font-size: 1.45rem;
    line-height: 1.1;
    font-weight: 600;
    margin: 0;
    color: #4f5f6f;
  }
}
.nonVisible {
  display: none;
}
.error-text {
  color: #4f5f6f;
  text-align: center;
}
.v-data-table {
  width: 100%;
}
.v-card__title {
  justify-content: space-between;
  border-bottom: 1px solid #d7dde4;
}
.time-range {
  button {
    margin: 5px 8px;
  }
  button.activeButton {
    -ms-filter: "progid:DXImageTransform.Microsoft.Shadow(Strength=10, Direction=0, Color=#49A8FE)";
    -moz-box-shadow: 0 0 10px 5px #49A8FE;
    -webkit-box-shadow: 0 0 10px 5px #49A8FE;
    box-shadow: 0 0 10px 5px #49A8FE;
    filter: progid:DXImageTransform.Microsoft.Shadow(Strength=10, Direction=135, Color=#49A8FE);
  }
}
.date-pickers_block {
  margin: 0;
}
.v-select.v-input--dense .v-chip {
  margin: 5px 4px;
}
