@import "~@/styles/_default.scss";









































































































































































































.v-card__title {
  .title {
    font-size: 1.1rem !important;
    color: #4f5f6f;
    line-height: 1.1;
    font-weight: 600;
  }
}
.table-bill {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.currency-content {
  display: flex;
}
.stat-icon {
  color: #52bcd3;
  display: inline-block;
  font-size: 26px;
  font-weight: 700;
  text-align: center;
  vertical-align: middle;
  width: 50px;
}
.stat {
  width: 35%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: inline-block;
  margin-right: 10px;
  vertical-align: middle;

  .value {
    font-size: 1.3vw;
    line-height: 24px;
    font-weight: 500;
    color: #4f5f6f;
  }
}
.stat-value,
.action_button {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: inline-block;
  margin-right: 10px;
  vertical-align: middle;

  font-size: 25px;
  line-height: 30px;
  font-weight: 500;
  color: #4f5f6f;

  .v-btn {
    margin: 0 5px 0 0;
  }
}
.two-currency {
  display: flex;
  justify-content: center;
  align-items: center;

  .stat-icon {
    width: 25px;
    font-size: 15px;
    color: rgba(0, 0, 0, .6);
  }
  .stat-value {
    font-size: 15px;
    line-height: inherit;
  }
}
