@import "~@/styles/_default.scss";
































































































































































































.title-block {
  overflow: hidden;
  padding-bottom: 15px;
  margin: 0 0 30px 0;
  border-bottom: 1px solid #d7dde4;

  .main_title {
    float: left;
    font-size: 1.45rem;
    line-height: 1.1;
    font-weight: 600;
    margin: 0;
    color: #4f5f6f;
  }
}
.bordered {
  border-bottom: 1px solid #d7dde4;
  margin-bottom: 16px;
}
