@import "~@/styles/_default.scss";





















.auth-block {
  width: 100%;
  margin: 0 auto;

  .v-card__title+.v-card__subtitle {
    margin-top: 0;
  }

  .auth-header {
    flex-direction: column;
    text-align: center;
    border-bottom: 1px solid #52bcd3;
    justify-content: center;

    .auth-title {
      color: #97a4b1;
      padding: 4px;
      line-height: 30px;
      font-size: 26px;
      font-weight: 600;
      margin: 0;
    }
  }
}
